	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>Install <a href=\"https://brew.sh\">homebrew</a></li>\n<li>run <code>xcode-select --install</code></li>\n<li>Install &#x26; init rbenv with <code>brew install rbenv &#x26;&#x26; brew install rbenv &#x26;&#x26; rbenv init</code></li>\n<li>List available ruby versions with <code>rbenv install -l</code></li>\n<li>Install preferred version with <code>rbenv install 2.5.1</code></li>\n<li>Check installed versions with  <code>rbenv versions</code>. There should be \"system\" and your installed version.</li>\n<li>Set version you want to use with <code>rbenv global 2.5.1</code></li>\n<li>Verify with <code>ruby -v</code></li>\n<li>Ensure that rbenv will be automatically run by adding <code>eval \"$(rbenv init -)\"</code> to your ~/.bash_profile</li>\n<li>Profit!</li>\n</ul>","id":"/Users/martinvich/repos/blog/src/pages/posts/how-to-install-ruby-on-rails-on-mac.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"How to install Ruby on Rails on Mac","date":"June 21, 2018","path":"/how-to-install-ruby-on-rails-on-mac","tags":["Ruby","Rails"]}}},"pathContext":{"siteTitle":"Martin Vích's blog","pathValue":"/how-to-install-ruby-on-rails-on-mac"}}

